import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome, faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { Col, Row, Image, Button, Container, Breadcrumb } from '@themesberg/react-bootstrap';
import { Link } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { ProfileCardWidget } from '../../components/Widgets'

import logo192 from "../../assets/img/favicon/android-chrome-192x192.png";
import ErrorImage from "../../assets/img/illustrations/500.svg";

import { Routes } from "../../routes";


export default () => {

  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);

  const apiKey = process.env.REACT_APP_API_KEY;
  let minusDate = Number(new Date().getDate());
  let minusMonth = Number(new Date().getMonth());

  let url = `https://newsapi.org/v2/everything?q=cardano&from=${new Date().getFullYear()}-${minusMonth + 1}-${minusDate}&sortBy=popularity&language=pt&apiKey=${apiKey}`;
  
  function fetchWeekNews(e) {    
    e.preventDefault();    
    let dt = new Date();
    dt.setDate( dt.getDate() - 6 );
    let start_week = dt.getFullYear() + '-' + Number(dt.getMonth() + 1) + '-' + dt.getDate();
    
    url = `https://newsapi.org/v2/everything?q=cardano&from=${start_week}&to=${new Date().getFullYear()}-${minusMonth + 1}-${minusDate}&sortBy=popularity&language=pt&apiKey=${apiKey}`;

    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result.articles);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      ) 
  }

  useEffect(() => {
    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result.articles);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])

  if (error) {
    return (
      <>
        <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
              <Breadcrumb.Item active>Notícias</Breadcrumb.Item>
            </Breadcrumb>
            <h4>Notícias</h4>
          </div>
        </div>
        <main>
          <div>Error: {error.message}</div>
        </main>
      </>
    );
  } else if (!isLoaded) {
    return (
      <>
        <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
              <Breadcrumb.Item active>Notícias</Breadcrumb.Item>
            </Breadcrumb>
            <h4>Notícias</h4>
          </div>
        </div>
        <main>
          <div>Carregando ...</div>
        </main>
      </>
    );
  } else if (items.length !== 0) {
    return (
      <>
        <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
              <Breadcrumb.Item active>Notícias</Breadcrumb.Item>
            </Breadcrumb>
            <h4>Notícias</h4>
          </div>
        </div>
        <main>
          {
            items.map((item,index) => {     
              let imageUrl = "";
              item.urlToImage ? imageUrl = item.urlToImage : imageUrl = logo192;
              return (
                <ProfileCardWidget index={index} imageUrl={imageUrl} title={item.title} content={item.content} sourceUrl={item.url} >
                </ProfileCardWidget>
              );
            })
          }
        </main>
      </>
    );
  } else {
    return (
      <>
        <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
              <Breadcrumb.Item active>Notícias</Breadcrumb.Item>
            </Breadcrumb>
            <h4>Notícias</h4>
          </div>
        </div>
        <main>
          <section className="vh-60 d-flex align-items-center justify-content-center">
            <Container>
              <Row className="align-items-center">
                <Col xs={12} lg={5} className="order-2 order-lg-1 text-center text-lg-left">
                  <h1 className="text-primary mt-5">
                    Não há notícias sobre <span className="fw-bolder">cardano</span> hoje.
                  </h1>
                  <p className="lead my-4">
                    Considere ver as notícias dos ultimos 7 dias!
                  </p>
                  <Button as={Link} variant="primary" className="animate-hover" to={Routes.DashboardOverview.path}>
                    <FontAwesomeIcon icon={faChevronLeft} className="animate-left-3 me-3 ms-2" />
                    Dashboard
                  </Button>
                  <Button variant="secondary" className="animate-hover mx-2" onClick={fetchWeekNews}>
                    Carregar notícias da semana
                    <FontAwesomeIcon icon={faChevronRight} className="animate-left-3 me-3 ms-2" />
                  </Button>
                </Col>
                <Col xs={12} lg={7} className="order-1 order-lg-2 text-center d-flex align-items-center justify-content-center">
                  <Image src={ErrorImage} className="img-fluid w-75" />
                </Col>
              </Row>
            </Container>
          </section>
        </main>
      </>
    );    
  }


};
