
import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCashRegister, faChartLine, faHome } from '@fortawesome/free-solid-svg-icons';
import { Col, Row, Breadcrumb} from '@themesberg/react-bootstrap';

import { CounterWidget, CircleChartWidget, BarChartWidget, TeamMembersWidget, ProgressTrackWidget, RankingWidget, SalesValueWidget, SalesValueWidgetPhone, AcquisitionWidget } from "../../components/Widgets";
import { PageVisitsTable } from "../../components/Tables";
import { trafficShares, totalOrders } from "../../data/charts";

export default (props) => {

  const { price, price_change, market, market_change, last_updated, high, low, circulating_supply, total_supply } = props;
  const [coin1, setCoin1] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin2, setCoin2] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin3, setCoin3] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin4, setCoin4] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin5, setCoin5] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin6, setCoin6] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin7, setCoin7] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin8, setCoin8] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin9, setCoin9] = useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [coin10,setCoin10]= useState({ "name" : "", "image" : { "thumb" : "" }, "market_data" : { "circulating_supply" : 0, "total_supply" : 0 } });
  const [PageVisits, SetPageVisits] = useState([]);

  let pageVisits = [];
  
  useEffect(() => {
    (function () {
      fetch('https://api.coingecko.com/api/v3/coins')
        .then(response => response.json())
        .then(json => {             
          setCoin1(json[0]);
          setCoin2(json[1]);
          setCoin3(json[2]);
          setCoin4(json[3]);
          setCoin5(json[4]);
          setCoin6(json[5]);
          setCoin7(json[6]);
          setCoin8(json[7]);
          setCoin9(json[8]);
          setCoin10(json[9]);
          pageVisits.push({ id: 1, views: json[0].market_data.current_price.brl, returnValue: json[0].market_data.market_cap.brl, bounceRate: json[0].market_data.market_cap_change_percentage_24h, pageName: json[0].name })
          pageVisits.push({ id: 2, views: json[1].market_data.current_price.brl, returnValue: json[1].market_data.market_cap.brl, bounceRate: json[1].market_data.market_cap_change_percentage_24h, pageName: json[1].name })
          pageVisits.push({ id: 3, views: json[2].market_data.current_price.brl, returnValue: json[2].market_data.market_cap.brl, bounceRate: json[2].market_data.market_cap_change_percentage_24h, pageName: json[2].name })
          pageVisits.push({ id: 4, views: json[3].market_data.current_price.brl, returnValue: json[3].market_data.market_cap.brl, bounceRate: json[3].market_data.market_cap_change_percentage_24h, pageName: json[3].name })
          pageVisits.push({ id: 5, views: json[4].market_data.current_price.brl, returnValue: json[4].market_data.market_cap.brl, bounceRate: json[4].market_data.market_cap_change_percentage_24h, pageName: json[4].name })
          pageVisits.push({ id: 6, views: json[5].market_data.current_price.brl, returnValue: json[5].market_data.market_cap.brl, bounceRate: json[5].market_data.market_cap_change_percentage_24h, pageName: json[5].name })
          pageVisits.push({ id: 7, views: json[6].market_data.current_price.brl, returnValue: json[6].market_data.market_cap.brl, bounceRate: json[6].market_data.market_cap_change_percentage_24h, pageName: json[6].name })
          pageVisits.push({ id: 8, views: json[7].market_data.current_price.brl, returnValue: json[7].market_data.market_cap.brl, bounceRate: json[7].market_data.market_cap_change_percentage_24h, pageName: json[7].name })
          pageVisits.push({ id: 9, views: json[8].market_data.current_price.brl, returnValue: json[8].market_data.market_cap.brl, bounceRate: json[8].market_data.market_cap_change_percentage_24h, pageName: json[8].name })
          pageVisits.push({ id: 10, views: json[9].market_data.current_price.brl, returnValue: json[9].market_data.market_cap.brl, bounceRate: json[9].market_data.market_cap_change_percentage_24h, pageName: json[9].name })
          SetPageVisits(pageVisits);
        })
        .catch((err) => {
          console.error(err.message);
        });
    })();
  }, [])

  return (
    <>
      <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item active>Dashboard</Breadcrumb.Item>
            </Breadcrumb>
          </div>
        </div>
      <Row className="justify-content-md-center">
        <Col xs={12} className="mb-4 d-none d-sm-block">
          <SalesValueWidget
            title="Cotação:"
            value={price}
            percentage={price_change}
          />
        </Col>
        <Col xs={12} className="mb-4 d-sm-none">
          <SalesValueWidgetPhone
            title="Cotação:"
            value={price}
            percentage={price_change}
          />
        </Col>
        <Col xs={12} sm={6} xl={4} className="mb-4">
          <CounterWidget
            category="Capitalização"
            title={market}
            period={last_updated}
            percentage={market_change}
            icon={faChartLine}
            iconColor="shape-secondary"
          />
        </Col>

        <Col xs={12} sm={6} xl={4} className="mb-4">
          <CounterWidget
            category="Fornecimento"
            title={circulating_supply}
            period={last_updated}
            percentage={ (circulating_supply * 100)/ total_supply }
            icon={faCashRegister}
            iconColor="shape-tertiary"
          />
        </Col>

        <Col xs={12} sm={6} xl={4} className="mb-4">
          <CircleChartWidget
            title="Stack Pools"
            data={trafficShares} />
        </Col>
      </Row>

      <Row>
        <Col xs={12} xl={12} className="mb-4">
          <Row>
            <Col xs={12} xl={8} className="mb-4">
              <Row>
                <Col xs={12} className="mb-4">
                  <PageVisitsTable pageVisits={PageVisits} />
                </Col>

                <Col xs={12} lg={6} className="mb-4">
                  <TeamMembersWidget />
                </Col>

                <Col xs={12} lg={6} className="mb-4">
                  <ProgressTrackWidget coin1={coin1} coin2={coin2} coin3={coin3} coin4={coin4} coin5={coin5}
                  coin6={coin6} coin7={coin7} coin8={coin8} coin9={coin9} coin10={coin10}/>
                </Col>
              </Row>
            </Col>

            <Col xs={12} xl={4}>
              <Row>
                <Col xs={12} className="mb-4">
                  <BarChartWidget
                    title="Rendimentos"
                    value={452}
                    percentage={18.2}
                    data={totalOrders} />
                </Col>

                <Col xs={12} className="px-0 mb-4">
                  <RankingWidget />
                </Col>

                <Col xs={12} className="px-0">
                  <AcquisitionWidget low={low} high={high} />
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
};
