export const SalesValueWidget = (props) => {
  const { title, value, percentage } = props;
  const percentageIcon = percentage < 0 ? faAngleDown : faAngleUp;
  const percentageColor = percentage < 0 ? "text-danger" : "text-success";
  const [dataStated, setData] = useState({ labels: [''], series: [[]] });

  function getWeekData() {
    let data = {
      labels: [],
      series: []
    };
    let series = [];
    let dt = new Date();
    dt.setDate( dt.getDate() - 6 );
    let url = `https://api.coingecko.com/api/v3/coins/cardano/market_chart/range?vs_currency=brl&from=${Math.floor(dt / 1000)}&to=${Math.floor(Date.now() / 1000)}`;
    fetch(url)
    .then(response => response.json())
    .then(json => {  
      console.log(json.prices);
      json.prices.map(item => {
        data.labels.push("");
        series.push(item[1].toFixed(2));
      })
      data.series.push(series);
      setData(data);
    })
    .catch((err) => {
      console.error(err.message);
    });
  }

  function getMonthData() {
    let data = {
      labels: [],
      series: []
    };
    let series = [];
    let dt = new Date();
    dt.setDate( dt.getDate() - 30 );
    let url = `https://api.coingecko.com/api/v3/coins/cardano/market_chart/range?vs_currency=brl&from=${Math.floor(dt / 1000)}&to=${Math.floor(Date.now() / 1000)}`;
    fetch(url)
    .then(response => response.json())
    .then(json => {  
      console.log(json.prices);
      json.prices.map(item => {
        data.labels.push("");
        series.push(item[1].toFixed(2));
      })
      data.series.push(series);
      setData(data);
    })
    .catch((err) => {
      console.error(err.message);
    });
  }

  return (
    <Card className="bg-secondary-alt shadow-sm">
      <Card.Header className="d-flex flex-row align-items-center flex-0">
        <div className="d-block">
          <h5 className="fw-normal mb-2">
            {title}
          </h5>
          <h3>{value}</h3>
          <small className="fw-bold mt-2">
            <span className="me-2">ultimas 24h: </span>
            <FontAwesomeIcon icon={percentageIcon} className={`${percentageColor} me-1`} />
            <span className={percentageColor}>
              {percentage.toFixed(2)}%
            </span>
          </small>
        </div>
        <div className="d-flex ms-auto">
          <Button variant="primary" size="sm" className="me-3" onClick={getWeekData}>Semanal</Button>
          <Button variant="secondary" size="sm" className="me-2" onClick={getMonthData}>Mensal</Button>
        </div>
      </Card.Header>
      <Card.Body className="p-2">
        <SalesValueChart data={dataStated}/>
      </Card.Body>
    </Card>
  );
};

